<template>
  <el-form ref="form" :model="form" label-width="80px">
    <el-form-item label="活动名称">
      <el-input v-model="form.name"></el-input>
    </el-form-item>
    <el-form-item label="活动区域">
      <el-select v-model="form.region" placeholder="请选择活动区域">
        <el-option label="区域一" value="上海"></el-option>
        <el-option label="区域二" value="北京"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="活动时间">
      <el-col :span="11">
        <el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
      </el-col>
      <el-col class="line" :span="2">-</el-col>
      <el-col :span="11">
        <el-time-picker placeholder="选择时间" v-model="form.date2" style="width: 100%;"></el-time-picker>
      </el-col>
    </el-form-item>
    <el-form-item label="即时配送">
      <el-switch v-model="form.delivery"></el-switch>
    </el-form-item>
    <el-form-item label="活动性质">
      <el-checkbox-group v-model="form.type">
        <el-checkbox label="美食/餐厅线上活动" name="type"></el-checkbox>
        <el-checkbox label="地推活动" name="type"></el-checkbox>
        <el-checkbox label="线下主题活动" name="type"></el-checkbox>
        <el-checkbox label="单纯品牌曝光" name="type"></el-checkbox>
      </el-checkbox-group>
    </el-form-item>
    <el-form-item label="特殊资源">
      <el-radio-group v-model="form.resource">
        <el-radio label="线上品牌商赞助"></el-radio>
        <el-radio label="线下场地免费"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="活动形式">
      <el-input type="textarea" v-model="form.desc"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button @click="handleCat" type="primary" style="margin-left: 16px;">
        立即创建
      </el-button>
      <el-button>取消</el-button>
    </el-form-item>
    <el-drawer :visible.sync="drawer" :direction="direction" :before-close="handleClose">
      <div class="boxe">
        <ul class="btue">
          <li>活动名称：{{ form.name }}</li>
          <li>活动区域：{{ form.region }}</li>
          <!-- <li>活动时间：{{ form.date1 }}</li> -->
          <li>及时配送：{{ form.delivery }}</li>
          <li>活动性质：{{ form.type }}</li>
          <li>特殊资源：{{ form.resource }}</li>
          <li>活动形式：{{ form.desc }}</li>
        </ul>
      </div>
    </el-drawer>
  </el-form>

</template>

<script>
export default {
  data() {
    return {
      drawer: false,
      direction: 'rtl',
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      }
    }
  },
  methods: {
    handleClose(done) {
      this.$confirm('确认关闭？')
        .then(() => {
          done();
        })
        .catch(() => { });
    },
    handleCat() {
      let pist = {
        name: this.form.name,
        region: this.form.region,
        date1: this.form.date1,
        date2: this.form.date2,
        delivery: this.form.delivery,
        type: this.form.type,
        resource: this.form.resource,
        desc: this.form.desc
      }
      console.log(pist);
      this.drawer = true;
    }
  }
}

</script>
<style>
li{
  list-style: none;
}
.boxe{
  width: 100%;
  height: 100%;
}
.btue li{
  width: 100%;
  height: 50px;
  margin: auto;
  float: left;
  border-bottom: 1px solid black;
}
</style>