import Vue from 'vue';
import VueRouter from 'vue-router';

// 导入组件
import Home from '../src/components/HomeWorld.vue';
import About from '../src/components/AboutWorld.vue';

Vue.use(VueRouter);

const routes = [
  { path: '/set', component: Home },
  { path: '/about', component: About }
];

const router = new VueRouter({
  routes
});

export default router;