import Vue from 'vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import App from './App.vue'
import router from '../router';
Vue.use(ElementUI);
Vue.directive("color",(n,i)=>{
  if (i.value % 2 == 0) {
    n.style.backgroundColor = "red";
  }
})
new Vue({
  router,
  el: '#app',
  render: h => h(App)
});
